"use client";

import { useEffect } from "react";
import { useRouter, useParams } from "next/navigation";
import { useUser } from "@clerk/nextjs";
import { supabase } from "@/utils/supabase/client";
import { toast } from "sonner";

export default function EditListing() {
  const { user, isLoaded } = useUser();
  const router = useRouter();
  const params = useParams();

  const listingId = Number(params.id); // ✅ correct way

  useEffect(() => {
    if (!isLoaded) return;
    if (!user) {
      router.replace("/");
      return;
    }

    // ❌ Invalid ID
    if (!listingId || isNaN(listingId)) {
      router.replace("/");
      return;
    }

    verifyUserRecord();
  }, [isLoaded, user, listingId]);

  const verifyUserRecord = async () => {
    const { data, error } = await supabase
      .from("listing") // ✅ lowercase
      .select("id")
      .eq("id", listingId)
      .eq("createdBy", user.id) // ✅ correct check
      .single();

    if (error || !data) {
      toast.error("You are not allowed to edit this listing");
      router.replace("/");
    }
  };

  return (
    <div className="px-10 md:px-36 my-10">
      <h2 className="font-bold text-2xl mb-6">
        Edit Listing #{listingId}
      </h2>

      {/* ✅ Your Formik form stays here */}
    </div>
  );
}
